.ProjectCard {
    background-color: #FFFFFF;
    border-radius: 1.25rem;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas:
        "cardImage cardTitle cardTitle"
        "cardDescription cardDescription cardDescription"
        "specificationLink launchApp launchSource";
    
        grid-template-rows: repeat(3, 6.25rem);
    width: 100%;

    box-shadow: 0.3125rem 0.3125rem 0.625rem;
    
}

.cardImage {
    grid-area: cardImage;
    margin: auto;
    /* height: 6.25rem; */
    
}

.cardTitle {
    grid-area: cardTitle;
    text-align: Left;
    margin: auto 0;
    
}

.cardDescription {
    grid-area: cardDescription;
    margin: 0 1.25rem;
}

.launchApp {
    grid-area: launchApp;
}

.launchSource {
    grid-area: launchSource;
}

.specification {
    grid-area: specificationLink
}

button {
    /* background-color: #40C8FF; */
    background-image: linear-gradient(#40C8FF, #5EE4EE);
    border: none;
    border-radius: 1.25rem;
    margin: 1.25rem;

    box-shadow: 0.1875rem 0.1875rem 0.25rem;
}

button:hover {
    cursor: pointer;
 }